.section {
    display: flex;
    justify-content: space-around;
    width: 100%;
    align-items: center;
    height: 100vh;
  }
  
  .block {
    width: 250px;
    height: 250px;
    padding: 60px;
    background-color: rgb(0, 0, 0);
  }
  
  .container {
    width: 500px;
  }
  
  p {
    font-size: 1.5rem;
  }
