*, *:before, *:after {
    transition: all 0.3s ease-in-out;
  }

.home-sec{
    height: 90vh;
    background-color: white;
}
  
.home-secBlack {
    height: 90vh;
    background-color: black;
}

.home-JoinsecBlack {
    
    background-color: black;
}

.homeFlex {
    position: relative;
    height: 90vh;
    display: flex;
    flex-direction: row;
}

.homeFlexReverse {
    position: relative;
    height: 90vh;
    display: flex;
    flex-direction: row;
}

.bg-Logo {
    flex: auto 1 1;
    width: 40vw;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 10vh;
    margin-right: 0vh;
}

.bg-Logo-right {
    flex: auto 1 1;
    width: 40vw;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 10vh;
    margin-right: 10vh;
}

.description-pos{
    margin-left: auto;
    margin-right: 10%;
    margin-top: auto;
    margin-bottom: auto;
}
  
.description-pos-left{
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 0%;
    margin-left: 12%;
}

.description-pos-right{
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 35%;
    margin-right: 10%;
}

.bg-Blob{
    flex: auto 1 1;
    position: relative;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 0%;
    margin-right: 5%;
}

.blob{
    z-index: 1;
    background-color: white;
    color: black;
    opacity: 1;
    font-size: 7vh;
    transition: all 0.2s ease-in-out;
    margin-left: auto;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: auto;
}

.blobReverse{
    z-index: 1;
    background-color: black;
    color: white;
    opacity: 1;
    font-size: 7vh;
    transition: all 0.2s ease-in-out;
    margin-left: 0%;
    margin-top: auto;
    margin-bottom: auto;
    margin-right: 0%;
}

.blob:hover{
    position: relative;
    z-index: 1;
    background-color: #dce2e9;
    color: #70ca32;
    opacity: 1;
    font-size: 8vh;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: black;
    margin: auto;
}

.blobReverse:hover{
    position: relative;
    z-index: 1;
    background-color: #dce2e9;
    color: #60d0ea;
    opacity: 1;
    font-size: 8vh;
    -webkit-text-stroke-width: 2px;
    -webkit-text-stroke-color: black;
    transition: all 0.2s ease-in-out;
    margin-top: 10vh;
    margin: auto;
}

.homeButton {
    position: relative;
    margin-top: 20vh;
    width: 50vw;
    flex: 1 1 auto;
    text-align: center;
}
  
.description-application {
    position: relative;
    margin-top: auto;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
}

.section-content{
    position: relative;
    height: 60vh;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: space-evenly;
    padding-bottom: 0%;
    padding-top: 0%;
    padding-right: 0%;
}

.description-pos-small{
    overflow: hidden;
    visibility: hidden;  
 }

 .smallBlobReverse{
   overflow:hidden;
   visibility: hidden;
}

.bg-smallBlob{
    overflow:hidden;
   visibility: hidden;
    margin-right: 0%;
    margin-left: 0%;
}

.bg-smallBlob{
    flex: auto 1 1;
    position: relative;
    margin: auto;
    overflow:hidden;
   visibility: hidden;
}

.smallBlob{
    overflow:hidden;
   visibility: hidden;
    
}





