.proj-intro {
  height: 100vh;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}

.proj-intro-title {
  margin-left: auto;
  margin-right: auto;
  color: white;
}

.proj-intro-links {
  position: absolute;
  right: 4vh;
  top: 96vh;
  color: rgba(255, 255, 255, 0.25);
}


.project-cardContainer {
  height: max-content;
  width: 600px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5vh;
  margin-bottom: 5vh;
  padding-bottom: 0.7cm;

  transition: 0.1s ease-in-out;
  font-size: 16px;
  margin-bottom: 100px;
  border-radius: 8px;
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.08);
}

.project-cardContainer-flex {
  display: flex;
  flex-direction: column;
}

.project-cardContainer:hover {
  box-shadow: 0 0 30px rgba(0, 0, 0, 0.18);
}

.project-place-img {
  text-align: center;
}

.project-img {
  height: 27vh;
  width: 600px;
  border-radius: 10px 10px 10px 10px;
}

.project-info {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.project-row-info {
  display: flex;
  flex-direction: column;
  text-align: left;
}

.project-font-h4 {
  font-size: 11px;
  line-height: 11px;
  margin-bottom: 9px;
  color: rgba(0, 0, 0, 0.5);
  margin-top: 33px;
  text-transform: none;
  font-weight: 400;
  font-family: monospace;
}

.project-font-name {
  margin: 10px 0 5px !important;
  font-size: 18px;
}

.project-font-authors {
  font-size: 12px;
}

.project-font-links {
  font-size: 12px;
}

.project-font-madewith {
  font-size: 12px;
}

.project-font-aboutcode-title {
  font-size: 14px;
}

.project-font-aboutcode-desc {
  font-size: 11px;
}

#sameLine h4,
#sameLine p {
  display: inline;
}

.project-font-brows-respons-dep {
  font-size: 12px;
}

.texts-left {
  margin-left: 0.5cm;
}

.texts-right {
  margin-right: 0.5cm;
}

.date-break {
  margin-top: 0.5cm;
}

.flexbox-column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: flex-start;
}

.flexbox-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-content: flex-start;
}

.italics {
  font-style: italic;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.box-shadow {
  box-shadow: 0 0 25px rgba(0, 0, 0, 0.08);
}











@media screen and (max-width: 960px) {
  * {
    text-align: center;

  }

  .project-cardContainer {
    height: max-content;
    width: 380px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 5vh;
    margin-bottom: 5vh;
    padding-bottom: 0.7cm;
  
    transition: 0.1s ease-in-out;
    font-size: 16px;
    margin-bottom: 100px;
    border-radius: 8px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.08);
  }
  
  .project-cardContainer-flex {
    display: flex;
    flex-direction: column;
  }
  
  .project-cardContainer:hover {
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.18);
  }
  
  .project-place-img {
    text-align: center;
  }
  
  .project-img {
    height: 27vh;
    width: 380px;
    border-radius: 10px 10px 10px 10px;
  }
  
  .project-info {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
  
  .project-row-info {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  
  .project-font-h4 {
    font-size: 12px;
    line-height: 0px;
    margin-bottom: 9px;
    color: rgba(0, 0, 0, 0.5);
    margin-top: 33px;
    text-transform: none;
    font-weight: 400;
    font-family: monospace;
  }
  
  .project-font-name {
    margin: 5px 0 5px !important;
    font-size: 18px;
  }
  
  .project-font-authors {
    font-size: 15px;
  }
  
  .project-font-links {
    font-size: 15px;
  }
  
  .project-font-madewith {
    font-size: 15px;
  }
  
  .project-font-aboutcode-title {
    font-size: 13px;
  }
  
  .project-font-aboutcode-desc {
    text-align: center;
    margin-right: 1.6%;
  
    font-size: 15px;
  }

  #sameLine h4,
  #sameLine p {
    display: inline;
  }
  
  .project-font-brows-respons-dep {
    font-size: 10px;
  }
  
  .texts-left {
    margin-left: 0cm;
  }
  
  .texts-right {
    margin-right: 0cm;
  }
  
  .date-break {
    margin-top: 0.5cm;
  }
  
  .flexbox-column {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: flex-start;
  }
  
  .italics {
    font-style: italic;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  
  .box-shadow {
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.08);
  }
  .flexbox-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-content: flex-start;
  }


}
